/* General Styles */
* {
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    font-family: 'Arial', sans-serif;
    background-color: #f9f9f9;
    overflow-x: hidden;
}

.container {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0;
}

/* Navigation */
.nav {
    height: 70px;
    background-color: #fff;
    padding: 0 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    width: 100%;
}

.nav-left, .nav-right {
    display: flex;
    align-items: center;
    gap: 20px;
}

.nav-links {
    display: flex;
    align-items: center;
    gap: 20px;
    flex-wrap: wrap;
}

.nav-link {
    text-decoration: none;
    color: #333;
    font-size: 16px;
    font-weight: 500;
    white-space: nowrap;
}

.nav-left-logo {
    font-size: 22px;
    font-weight: bold;
    color: #007bff;
}

.sign-up {
    background-color: #007bff;
    color: white;
    padding: 8px 16px;
    border-radius: 5px;
}

.sign-up:hover {
    background-color: #0056b3;
}

/* Hamburger Menu */
.hamburger {
    display: none;
    background: none;
    border: none;
    font-size: 24px;
    cursor: pointer;
    padding: 10px;
}

/* Responsive Navigation */
@media (max-width: 768px) {
    .hamburger {
        display: block;
    }

    .nav-links {
        display: none;
        flex-direction: column;
        position: absolute;
        top: 70px;
        left: 0;
        width: 100%;
        background: white;
        box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
        padding: 10px 0;
    }

    .nav-links.open {
        display: flex;
    }

    .nav-links a {
        padding: 10px;
        text-align: center;
        width: 100%;
        display: block;
    }
}

/* Hero Sections */
.first-hero {
    background: linear-gradient(to right, #1c89e9, #00cbfe);
    height: 400px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    color: white;
    width: 100%;
}

.first-hero h1 {
    font-size: 36px;
    font-weight: bold;
}

.first-hero p {
    font-size: 18px;
    max-width: 600px;
    padding: 0 10px;
}

/* CTA Button */
.cta-button {
    margin-top: 15px;
    padding: 12px 24px;
    font-size: 18px;
    color: white;
    background-color: #28a745;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.cta-button:hover {
    background-color: #218838;
}

/* Sections with Different Colors */
.content-section {
    text-align: center;
}
.second-hero {
    background-color: #ffebcc; /* Light Orange */
    padding: 60px 20px;
}

.why-us {
    background-color: #d4edda; /* Light Green */
    padding: 60px 20px;
}

.faq {
    background-color: #cce5ff; /* Light Blue */
    padding: 60px 20px;
}

.about {
    background-color: #f8d7da; /* Light Red */
    padding: 60px 20px;
}

.contact {
    background-color: #e2e3e5; /* Light Gray */
    padding: 60px 20px;
}

.surveys {
    background-color: #fff3cd; /* Soft Yellow */
    padding: 60px 20px;
}

.blog {
    background-color: #d1c4e9; /* Soft Purple */
    padding: 60px 20px;
}

h2 {
    font-size: 28px;
    color: #333;
    text-align: center;
}

ul {
    list-style: none;
    padding: 0;
}

ul li {
    font-size: 18px;
    margin: 8px 0;
    text-align: center;
}

/* Footer */
.footer {
    background-color: #222;
    color: white;
    padding: 20px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 100%;
}

.footer a {
    color: white;
    text-decoration: none;
}

.footer a:hover {
    text-decoration: underline;
}
